*,*::before,*::after{padding:0;margin:0;border:0;box-sizing:border-box}html,body,#root{height:100%}body{font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;font-size:16px;font-weight:400;line-height:1.3;color:#333;background-color:#fff;overflow-x:hidden;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}a{color:inherit;text-decoration:none}img{max-width:100%;height:auto}ol,ul,li{list-style:none}.container-fluid{width:100%;padding-right:16px;padding-left:16px;margin-right:auto;margin-left:auto}.container{width:100%;max-width:1200px;padding-right:16px;padding-left:16px;margin-right:auto;margin-left:auto}@media (min-width: 768px){.container{padding-right:24px;padding-left:24px}}@media (min-width: 1024px){.container{padding-right:32px;padding-left:32px}}.row{display:flex;flex-wrap:wrap;margin-right:-16px;margin-left:-16px}h1,h2,h3,h4,h5,h6{font-family:inherit;line-height:1.3;color:inherit}h1{font-size:32px}h2{font-size:24px}h3{font-size:18px}h4{font-size:16px}h5{font-size:14px}h6{font-size:12px}
